import PropTypes from 'prop-types';
import { useId } from 'react';
import { styled } from 'styled-components';

import { getLuma } from '#utils/colors';

const CrossHatch = ({ id }) => {
  return (
    <pattern
      id={id}
      width="4"
      height="4"
      patternTransform="rotate(45 0 0)"
      patternUnits="userSpaceOnUse"
    >
      <line x1="0" y1="0" x2="0" y2="4" stroke="#EEEEEE" strokeWidth="2" />
    </pattern>
  );
};

CrossHatch.propTypes = {
  id: PropTypes.string.isRequired,
};

const Wrapper = styled.svg`
  display: block;
`;

const Swatch = ({
  className,
  fill = 'transparent',
  width = 158,
  height = 58,
  cornerRadius = 0,
}) => {
  const x = 0;
  const y = 0;
  const crosshatchID = `cross-hatch-${useId()}`;

  // When the colour is too close to white then we need to add a border
  const lumaThreshold = 240;
  const luma = getLuma(fill);
  const needsBorder = luma > lumaThreshold || fill === 'transparent';

  return (
    <Wrapper
      className={className}
      width={width}
      height={height}
      viewBox={`0 0 ${width} ${height}`}
    >
      <CrossHatch id={crosshatchID} />
      <rect
        fill={`url(#${crosshatchID})`}
        x="0"
        y="0"
        width="100%"
        height="100%"
        rx={cornerRadius}
      />
      <rect
        fill={fill}
        x={x}
        y={y}
        width="100%"
        height="100%"
        rx={cornerRadius}
      />
      {needsBorder && (
        <rect
          fill="transparent"
          x={x}
          y={y}
          width="100%"
          height="100%"
          rx={cornerRadius}
          stroke="#DDDDDD"
          strokeWidth="1"
        />
      )}
    </Wrapper>
  );
};

Swatch.propTypes = {
  className: PropTypes.string,
  fill: PropTypes.string.isRequired,
  width: PropTypes.number,
  height: PropTypes.number,
  cornerRadius: PropTypes.number,
};

export default Swatch;
